import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {Redirect, withRouter} from 'react-router-dom';

import {Link} from "react-router-dom";
import { handleAuthentication } from '../../actions/auth-action'
import { addProduct, getAllProductsBySeller } from '../../actions/product-actions'
import Navbar from "../../components/Navbar/Navbar";
import Loader from "../App/Loader";
import {
    Form,
    Input,
    Button,
    Breadcrumb,
    Divider,
    Select,
    InputNumber,
    Radio
} from 'antd';
import { CATEGORIES } from '../../actions/constants'
import { CloseOutlined } from '@ant-design/icons';
import Footer from "../../components/Footer/Footer";

const { Option } = Select

class AddSellerProduct extends Component {

    constructor(props){
        super(props);
        this.state={
            subCategories:[],
            images:[],
            gender:"NA"
        }
    }

    componentDidMount() {
        if(!this.props.auth.isAuthenticated){
            this.props.handleAuthentication();
        }
        this.props.getAllProductsBySeller(localStorage.getItem("cvd_email"))
    }


    showWidget(widget){
        widget.open()
    }

    setSubcategories(category){
        for(let i=0; i<CATEGORIES.length; i++){
            if(CATEGORIES[i].name===category){
                this.setState({subCategories:CATEGORIES[i].subCategories})
            }
        }
    }

    onFinish = values => {
        console.log('Received values of form: ', values);
        let payload = {
            "name": this.state.name,
            "description": this.state.description,
            "price": parseInt(this.state.price),
            "brand": this.state.brand,
            "color": this.state.color,
            "material": this.state.material,
            "size": this.state.size,
            "usage": this.state.usage?this.state.usage:"NA",
            "minimumOrderQuantity": this.state.minimumOrderQuantity.toString(),
            "gender": this.state.gender?this.state.gender:"NA",
            "deliveryTime": this.state.deliveryTime.toString(),
            "packagingDetails": this.state.packagingDetails?this.state.packagingDetails:"NA",
            "availableToSellQty": this.state.availableToSellQty,
            "images": this.state.images,
            "reservedQty": "0",
            "category": this.state.category,
            "subCategory": this.state.subCategory,
            "isActive": this.state.isActive,
            "status": "PENDING",
            "sellerId": this.props.products.userSeller._id,
            "sellerName": this.props.products.userSeller.name,
            "isTrending": false,
            "createTs": new Date().toString()
        }
        console.log(payload)
        this.props.addProduct(payload)
        this.props.history.push("/sellerPortal")
    };

    formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 18 },
    };

    removeImage(image){
        let images = this.state.images;
        const index = images.indexOf(image);
        if (index > -1) {
            images.splice(index, 1);
        }
        this.setState({images:images});
    }

    render() {
        let { auth, products  } = this.props;

        let uploadWidget = window.cloudinary.createUploadWidget({
                cloudName: 'dbeo3p8ma',
                uploadPreset: 'yu0bdp1e',
                sources: [ 'local'],
                multiple:false}, (error, result) => {
                if (!error && result && result.event === "success") {
                    let images = this.state.images;
                    images.push(result.info.url)
                    this.setState({images:images})
                }
            }
        )

        return (

            <div>
                <Navbar auth={auth}/>
                {products.loading && <Loader/>}
                {!auth.isAuthenticated?<Redirect to={"/"} />:
                <div className={"container"}>
                    <Breadcrumb>
                        <Breadcrumb.Item>
                            <Link to="/">Home</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>
                            <Link to="/sellerPortal">Seller Console</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>Add Product</Breadcrumb.Item>
                    </Breadcrumb>
                    <Divider/>
                    <div className={"row"}>

                        <div className={"col-md-12"}>
                            <div className={"addProductContainer"}>
                                <div className={"addProductHead"}>Add Product</div>
                                <div className={"addProductText"}>Products added from here will be sent to administrators for approval.</div>
                                <Divider className={"divider"}/>
                                <Form
                                    name="validate_other"
                                    {...this.formItemLayout}
                                    onFinish={this.onFinish}
                                >
                                    <div className={"row"}>
                                        <div className={"col-md-12"}>
                                            <Form.Item labelCol={{ span: 3 }} wrapperCol={{ span: 21 }} label="Product Name" name="name" rules={[{ required: true, message: 'Please input your product name!' }]}>
                                                <Input  style={{width:'100%'}} onChange={(text)=>this.setState({name:text.target.value})}/>
                                            </Form.Item>
                                            <Form.Item labelCol={{ span: 3 }} wrapperCol={{ span: 21 }} label="Description" name="description" type="textarea" rules={[{ required: true, message: 'Please input your product description!' }]}>
                                                <Input.TextArea  style={{width:'100%'}} onChange={(text)=>this.setState({description:text.target.value})}/>
                                            </Form.Item>
                                        </div>
                                    </div>
                                    <div className={"row"}>
                                        <div className={"col-md-6"}>

                                            <Form.Item
                                                name="category"
                                                label="Category"
                                                hasFeedback
                                                rules={[{ required: true, message: 'Please select product\'s category!' }]}
                                            >
                                                <Select placeholder="Please select product's category!" onChange={(category) => {
                                                    this.setState({category:category})
                                                    this.setSubcategories(category)
                                                }}>
                                                    {
                                                        CATEGORIES.map((item, index) => (
                                                            <Option value={item.name}>{item.name}</Option>
                                                        ))
                                                    }
                                                </Select>
                                            </Form.Item>
                                            <Form.Item label="Price" name="price" rules={[{ required: true, message: 'Please input your product price!' }]}>
                                                <Input  style={{width:'100%'}} onChange={(text)=>this.setState({price:text.target.value})}/>
                                            </Form.Item>
                                            <Form.Item label="Brand" name="brand" rules={[{ required: true, message: 'Please input your product brand!' }]}>
                                                <Input  style={{width:'100%'}} onChange={(text)=>this.setState({brand:text.target.value})}/>
                                            </Form.Item>
                                            <Form.Item label="Size" name="size" rules={[{ required: true, message: 'Please input your product size!' }]}>
                                                <Input  style={{width:'100%'}} onChange={(text)=>this.setState({size:text.target.value})}/>
                                            </Form.Item>
                                            <Form.Item label="Usage" name="usage">
                                                <Input  style={{width:'100%'}} onChange={(text)=>this.setState({usage:text.target.value})}/>
                                            </Form.Item>

                                            {products.userSeller && <Form.Item label="Seller" name="sellerId" rules={[{ required: true, message: 'Please input seller.' }]} initialValue={products.userSeller.name}>
                                                <Input  style={{width:'100%'}} disabled />
                                            </Form.Item>}
                                            <Form.Item label="MOQ" name="minimumOrderQuantity" rules={[{ required: true, message: 'Please input minimum order qty!' }]}>
                                                <InputNumber  style={{ width: "100%" }} placeholder={"Enter minimum order quantity."} min={1} max={100000000} onChange={(text)=>this.setState({minimumOrderQuantity:text})}/>
                                            </Form.Item>
                                            <Form.Item label={"Add Images"}>
                                                <Button style={{backgroundColor:"rgb(224, 224, 224)", color:"#3c3c3c"}} onClick={()=> {
                                                    if(this.state.images.length<3){
                                                        this.showWidget(uploadWidget)
                                                    }
                                                    else alert("Only 3 images are permitted per product.")
                                                }}>
                                                    Upload
                                                </Button>
                                                <div className={"productImages"}>
                                                    <div className={"row"}>

                                                        {
                                                            this.state.images.map((item, index) => (
                                                                <div className={"col-md-4"}>
                                                                    <div className={"productImmage"} key={index}>
                                                                        <img src={item}/>
                                                                        <div className={"removeImage"} onClick={()=>this.removeImage(item)}><CloseOutlined /></div>
                                                                    </div>
                                                                </div>
                                                            ))
                                                        }
                                                    </div>
                                                </div>
                                            </Form.Item>
                                            <Form.Item wrapperCol={{ span: 12 }}>
                                                <Button type="primary" htmlType="submit" className={"addSellerSubmitButton"}>
                                                    Submit
                                                </Button>
                                            </Form.Item>
                                        </div>
                                        <div className={"col-md-6"}>

                                            <Form.Item
                                                name="subCategory"
                                                label="Sub Category"
                                                hasFeedback
                                                rules={[{ required: true, message: 'Please select product\'s sub category!' }]}
                                            >
                                                <Select placeholder="Please select product's sub category!" onChange={(subCategory) => {
                                                    this.setState({subCategory:subCategory})
                                                }}>
                                                    {
                                                        this.state.subCategories.map((item, index) => (
                                                            <Option value={item}>{item}</Option>
                                                        ))
                                                    }
                                                </Select>
                                            </Form.Item>
                                            <Form.Item label="Color" name="color" rules={[{ required: true, message: 'Please input your product color!' }]}>
                                                <Input  style={{width:'100%'}} onChange={(text)=>this.setState({color:text.target.value})}/>
                                            </Form.Item>
                                            <Form.Item label="Material" name="material" rules={[{ required: true, message: 'Please input your product material!' }]}>
                                                <Input  style={{width:'100%'}} onChange={(text)=>this.setState({material:text.target.value})}/>
                                            </Form.Item>
                                            <Form.Item label="Package Details" name="packagingDetails">
                                                <Input  style={{width:'100%'}} onChange={(text)=>this.setState({packagingDetails:text.target.value})}/>
                                            </Form.Item>
                                            <Form.Item label="Delivery Time" name="deliveryTime" rules={[{ required: true, message: 'Please input delivery time in days.' }]}>
                                                <Input style={{ width: "100%" }} onChange={(text)=>this.setState({deliveryTime:text.target.value})}/>
                                            </Form.Item>
                                            <Form.Item label="Available Qty" name="availableToSellQty" rules={[{ required: true, message: 'Please input delivery time!' }]}>
                                                <InputNumber style={{ width: "100%" }} placeholder={"Enter available to sell qty."} min={1} max={1000000000} onChange={(text)=>this.setState({availableToSellQty:text})}/>
                                            </Form.Item>
                                            <Form.Item name="radio-button" label="Status" rules={[{ required: true, message: 'Please select the product status' }]}>
                                                <Radio.Group onChange={(text)=>this.setState({isActive:text.target.value==="Active"?true:false})}>
                                                    <Radio.Button value="Active">Active</Radio.Button>
                                                    <Radio.Button value="Inactive">Inactive</Radio.Button>
                                                </Radio.Group>
                                            </Form.Item>
                                        </div>
                                    </div>
                                </Form>
                            </div>
                        </div>
                    </div>

                </div>
                }
                <Footer/>
            </div>
        );
    }
}

AddSellerProduct.propTypes = {
    handleAuthentication:PropTypes.func.isRequired,
    addNewCustomer:PropTypes.func.isRequired,
    addProduct:PropTypes.func.isRequired,
    auth: PropTypes.object,
    sellers:PropTypes.object,
    products:PropTypes.object
}

const mapStateToProps = state  => ({
    getAllProductsBySeller:PropTypes.func.isRequired,
    auth:state.auth,
    products:state.products,
    sellers:state.sellers
})

export default connect(mapStateToProps, {handleAuthentication,  addProduct, getAllProductsBySeller})(withRouter(AddSellerProduct))

