import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import {Redirect, withRouter} from 'react-router-dom';
import {Link} from "react-router-dom";
import { getCustomers, updateCustomerById } from '../../actions/customers-action'
import { getSellers, updateSellerById, getPendingSellers } from '../../actions/seller-actions'
import { handleAuthentication } from '../../actions/auth-action'
import Navbar from "../../components/Navbar/Navbar";
import Loader from "../App/Loader";
import { PlusOutlined } from '@ant-design/icons';

import {
    Table,
    Tag,
    Divider,
    Button,
    Space,
    Breadcrumb,
    Switch,
    Modal,
    Form,
    Input,
    Radio,
    Select
} from 'antd';
import './SellerManager.css';
import Footer from "../../components/Footer/Footer";
import {STATES} from "../../helpers/cities";
import { CloseOutlined } from '@ant-design/icons';

const PAYMENT_MODES = ["Cash","Credit Card","Cheque","DD","Invoice","Online"]
const { Option } = Select

class SellerManager extends Component {

    constructor(props){
        super(props);
        this.state={
            showActiveOnly:true,
            columns:[
                {
                    title: 'Name',
                    dataIndex: 'name',
                    key: 'name',
                    render: text => <a>{text}</a>,
                },
                {
                    title: 'Email',
                    dataIndex: 'email',
                    key: 'email',
                },
                {
                    title: 'Contact',
                    dataIndex: 'contact',
                    key: 'contact',
                },
                {
                    title: 'Referred By',
                    dataIndex: 'createdByUserName',
                    key: 'createdByUserName',
                },
                {
                    title: 'GST Certificate',
                    dataIndex: 'gstFileUrl',
                    key: 'gstFile',
                    render: gstFile => (
                        <a href={gstFile} target={"blank"}>{gstFile?"GST":"-"}</a>
                    ),
                },
                {
                    title: 'Status',
                    key: 'status',
                    dataIndex: 'status',
                    render: status => (
                        <Tag color={status==="PENDING"?'geekblue':'green'}>
                            {status.toUpperCase()}
                        </Tag>
                    )
                },
                {
                    title: 'Actions',
                    key: 'action',
                    render: (text, record) => (
                        <Space size="middle">
                            <Button type="primary" shape="round" size={"small"}>
                                <Link to={"viewSeller/" + record._id}>View</Link>
                            </Button>
                            <Button type="primary" shape="round" size={"small"} onClick={()=>this.approveSeller(record, "APPROVED")}>
                                Approve
                            </Button>
                            <Button type="danger" shape="round" size={"small"} onClick={()=>this.approveSeller(record, "REJECTED")}>
                                Reject
                            </Button>
                        </Space>
                    ),
                },
            ],
            approvedSellersColumns:[
                {
                    title: 'Name',
                    dataIndex: 'name',
                    key: 'name',
                    render: text => <a>{text}</a>,
                },
                {
                    title: 'Email',
                    dataIndex: 'email',
                    key: 'email',
                },
                {
                    title: 'Contact',
                    dataIndex: 'contact',
                    key: 'contact',
                },
                {
                    title: 'GSt Number',
                    dataIndex: 'gstNumber',
                    key: 'gstNumber',
                },
                {
                    title: 'GST Certificate',
                    dataIndex: 'gstFile',
                    key: 'gstFile',
                    render: gstFile => (
                        <a href={gstFile} target={"blank"}>{gstFile?"GST":"-"}</a>
                    ),
                },
                {
                    title: 'Status',
                    key: 'isActive',
                    dataIndex: 'isActive',
                    render: isActive => (
                        <Tag color={isActive===false?'volcano':'green'}>
                            {isActive==true?"ACTIVE":"INACTIVE"}
                        </Tag>
                    )
                },
                {
                    title: 'Actions',
                    key: 'action',
                    render: (text, record) => (
                        <Space size="middle">
                            <Button type="primary" shape="round" size={"small"}>
                                <Link to={"viewSeller/" + record._id}>View</Link>
                            </Button>
                            <Button type={record.isActive==true?"danger":"primary"} shape="round" size={"small"} onClick={()=>this.changeSellerStatus(record)}>
                                {record.isActive==true?"Deactivate":"Activate"}
                            </Button>
                        </Space>
                    ),
                },
            ],
            pendingSellersRequestColumns:[
                {
                    title: 'Name',
                    dataIndex: 'name',
                    key: 'name',
                    render: text => <a>{text}</a>,
                },
                {
                    title: 'Email',
                    dataIndex: 'email',
                    key: 'email',
                    render: text => <a>{text}</a>,
                },
                {
                    title: 'Contact',
                    dataIndex: 'contact',
                    key: 'contact',
                    render: text => <a>{text}</a>,
                },
                {
                    title: 'Actions',
                    key: 'action',
                    render: (text, record) => (
                        <Space size="middle">
                            <Link to={"aproveSellerRequest/" + record.name + "/"+record.email + "/" + record.contact}>
                                <Button type="primary" shape="round" size={"small"}>
                                    Approve
                                </Button>
                            </Link>
                            <Button type="danger" shape="round" size={"small"} onClick={()=>this.updateCustomerStatus(record, "SELLER_REJECTED")}>
                                Reject
                            </Button>
                        </Space>
                    ),
                },
            ],
            ModalText: 'Content of the modal',
            visible: false,
            confirmLoading: false,
            selectedSellerName: "Arjun",
            selectedSellerContact:"",
            selectedSellerEmail:""
        }
    }

    updateCustomerStatus = (customer, status) =>{

    }

    approveSeller(seller, newStatus){
        seller.status = newStatus
        this.props.updateSellerById(seller._id, seller);
    }

    changeSellerStatus(seller){
        seller.isActive = !seller.isActive
        this.props.updateSellerById(seller._id, seller);
    }
    componentDidMount() {
        if(!this.props.auth.isAuthenticated){
            this.props.handleAuthentication();
        }
        this.props.getSellers()
        this.props.getPendingSellers();
    }

    getReferralSellers(sellers){
        let result = [];
        if(sellers){
            for(let i=0; i<sellers.length; i++){
                if(sellers[i].status==="PENDING")
                    result.push(sellers[i])
            }
        }
        return result
    }
    getApprovedSellers(sellers){
        let result = [];
        if(sellers){
            if(this.state.showActiveOnly==true){
                for(let i=0; i<sellers.length; i++){
                    if(sellers[i].status!=="PENDING" && sellers[i].isActive==true)
                        result.push(sellers[i])
                }
            }
            else{
                for(let i=0; i<sellers.length; i++){
                    if(sellers[i].status!=="PENDING")
                        result.push(sellers[i])
                }
            }
        }
        return result
    }
    getActiveSellers(sellers){
        let result = 0;
        if(sellers){
            for(let i=0; i<sellers.length; i++){
                if(sellers[i].isActive===true)
                   result++;
            }
        }
        return result
    }
    getInactiveSellers(sellers){
        let result = 0;
        if(sellers){
            for(let i=0; i<sellers.length; i++){
                if(sellers[i].isActive!==true)
                    result++;
            }
        }
        return result
    }
    getPendingSellers(sellers){
        let result = 0;
        if(sellers){
            for(let i=0; i<sellers.length; i++){
                if(sellers[i].status==="PENDING")
                    result++;
            }
        }
        return result
    }
    getSellersMissingGst(sellers){
        let result = 0;
        if(sellers){
            for(let i=0; i<sellers.length; i++){
                if(!sellers[i].gstFile)
                    result++;
            }
        }
        return result
    }
    showWidget(widget){
        widget.open()
    }


    onFinish = values => {
        console.log('Received values of form: ', values);
        let payload = {
            name:this.state.name,
            about:this.state.about,
            address:this.state.address,
            addressState:this.state.addressState,
            addressCity:this.state.addressCity,
            email:this.state.email,
            contact:this.state.contact,
            paymentModes:this.state.paymentModes,
            isActive:this.state.isActive,
            status:"APPROVED",
            gstNumber:this.state.gstNumber,
            gstFile:this.state.gstFile,
            createdBy: this.props.auth.user._id
        }
        console.log(payload)
        this.props.addNewSeller(payload)
        this.props.history.push("/sellerManager")
    };

    formItemLayout = {
        labelCol: { span: 6 },
        wrapperCol: { span: 18 },
    };


    removeImage(image){
        this.setState({gstFile:""});
    }
    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = () => {
        this.setState({
            ModalText: 'The modal will be closed after two seconds',
            confirmLoading: true,
        });
        setTimeout(() => {
            this.setState({
                visible: false,
                confirmLoading: false,
            });
        }, 2000);
    };

    handleCancel = () => {
        console.log('Clicked cancel button');
        this.setState({
            visible: false,
        });
    };


    render() {
        let { auth,sellers  } = this.props;
        let uploadWidget = window.cloudinary.createUploadWidget({
                cloudName: 'dbeo3p8ma',
                uploadPreset: 'yu0bdp1e',
                sources: [ 'local'],
                multiple:false}, (error, result) => {
                if (!error && result && result.event === "success") {
                    this.setState({gstFile:result.info.url})
                }
            }
        )
        return (
            <div>
                <Navbar auth={auth}/>
                {sellers.loading && <Loader/>}
                {!auth.isAuthenticated?<Redirect to={"/"} />:""}
                <Modal
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    confirmLoading={this.state.confirmLoading}
                    onCancel={this.handleCancel}
                    width={"80%"}
                    centered={true}
                    style={{marginTop:'1.5rem'}}
                    footer={[]}
                >
                    <div className={"addProductContainer"}>
                        <div className={"addProductText"}>Add below information to approve seller request.</div>
                        <Divider className={"divider"}/>
                        <Form
                            name="validate_other"
                            {...this.formItemLayout}
                            onFinish={this.onFinish}
                        >
                            <div className={"row"}>
                                <div className={"col-md-12"}>
                                    <Form.Item labelCol={{ span: 3 }} wrapperCol={{ span: 21 }} label="Seller Name" name="name" rules={[{ required: true, message: 'Please input your product name.'}]}>
                                        <Input style={{width:'100%'}} onChange={(text)=>this.setState({name:text.target.value})} />
                                    </Form.Item>
                                    <Form.Item labelCol={{ span: 3 }} wrapperCol={{ span: 21 }} label="About Seller" name="description" type="textarea" rules={[{ required: true, message: 'Please input your product description.' }]}>
                                        <Input.TextArea  style={{width:'100%'}} onChange={(text)=>this.setState({about:text.target.value})}/>
                                    </Form.Item>
                                    <Form.Item labelCol={{ span: 3 }} wrapperCol={{ span: 21 }} label="Address" name="address" rules={[{ required: true, message: 'Please input registered address.' }]}>
                                        <Input  style={{width:'100%'}} onChange={(text)=>this.setState({address:text.target.value})}/>
                                    </Form.Item>
                                </div>
                            </div>
                            <div className={"row"}>
                                <div className={"col-md-6"}>
                                    <Form.Item
                                        name="addressState"
                                        label="State"
                                        hasFeedback
                                        rules={[{ required: true, message: 'Please select the state.' }]}
                                    >
                                        <Select placeholder="Please select the state" onChange={(addressState) => {
                                            this.setState({addressState:addressState})
                                        }}>
                                            {
                                                STATES.map((item, index) => (
                                                    <Option value={item} key={index}>{item}</Option>
                                                ))
                                            }
                                        </Select>
                                    </Form.Item>
                                    <Form.Item label="Phone" name="contact" rules={[{ required: true, message: 'Please input contact number.' }]}>
                                        <Input  style={{width:'100%'}} onChange={(text)=>this.setState({contact:text.target.value})}/>
                                    </Form.Item>
                                    <Form.Item
                                        name="paymentModes"
                                        label="Payment Modes"
                                        hasFeedback
                                        rules={[{ required: true, message: 'Please select payment modes.' }]}
                                    >
                                        <Select mode="multiple" placeholder="Please select at least one payment mode." onChange={(paymentModes) => {
                                            this.setState({paymentModes:paymentModes})
                                        }}>
                                            {
                                                PAYMENT_MODES.map((item, index) => (
                                                    <Option value={item} key={index}>{item}</Option>
                                                ))
                                            }
                                        </Select>
                                    </Form.Item>
                                    <Form.Item name="radio-button" label="Status" rules={[{ required: true, message: 'Please select the product status.' }]}>
                                        <Radio.Group onChange={(text)=>this.setState({isActive:text.target.value==="Active"?true:false})}>
                                            <Radio.Button value="Active">Active</Radio.Button>
                                            <Radio.Button value="Inactive">Inactive</Radio.Button>
                                        </Radio.Group>
                                    </Form.Item>
                                    <Form.Item wrapperCol={{ span: 12}} >
                                        <Button type="primary" htmlType="submit" className={"addSellerSubmitButton"}>
                                            Submit
                                        </Button>
                                    </Form.Item>
                                </div>
                                <div className={"col-md-6"}>
                                    <Form.Item label="City" name="addressCity" rules={[{ required: true, message: 'Please input the city.' }]}>
                                        <Input  style={{width:'100%'}} onChange={(text)=>this.setState({addressCity:text.target.value})}/>
                                    </Form.Item>
                                    <Form.Item label="Email" name="email" rules={[{ type:"email", required: true, message: 'Please input a valid email.' }]}>
                                        <Input  style={{width:'100%'}} onChange={(text)=>this.setState({email:text.target.value})}/>
                                    </Form.Item>
                                    <Form.Item label="GST Number" name="gstNumber" rules={[{ required: true, message: 'Please input the GST Number.' }]}>
                                        <Input  style={{width:'100%'}} onChange={(text)=>this.setState({gstNumber:text.target.value})}/>
                                    </Form.Item>
                                    <Form.Item label={"GST Certificate"}>
                                        <Button style={{backgroundColor:"rgb(224, 224, 224)", color:"#3c3c3c"}} onClick={()=> {
                                            this.showWidget(uploadWidget)
                                        }}>
                                            Upload
                                        </Button>
                                        {this.state.gstFile && <div style={{marginTop:"1rem"}}>
                                            <a href={this.state.gstFile} target={"blank"} style={{fontWeight:"bold"}}>View Certificate</a>
                                            <div className={"removeImage"} onClick={()=>this.removeImage()}><CloseOutlined /></div>
                                        </div>}
                                    </Form.Item>
                                </div>
                            </div>
                        </Form>
                    </div>
                </Modal>
                <div className={"container"}>
                    <Breadcrumb>
                        <Breadcrumb.Item>
                            <Link to="/">Home</Link>
                        </Breadcrumb.Item>
                        <Breadcrumb.Item>Seller Manager</Breadcrumb.Item>
                    </Breadcrumb>
                    <Divider />
                    <div className={"row"}>
                        <div className={"col-md-3"}>
                            <div className={"sellerStat"}>
                                <div className={"statNumber"}>{this.getActiveSellers(sellers.sellers)}</div>
                                <div className={"statDetail"}>Active Sellers</div>
                            </div>
                        </div>
                        <div className={"col-md-3"}>
                            <div className={"sellerStat"}>
                                <div className={"statNumber"}>{this.getInactiveSellers(sellers.sellers)}</div>
                                <div className={"statDetail"}>Inactive Sellers</div>
                            </div>
                        </div>
                        <div className={"col-md-3"}>
                            <div className={"sellerStat"}>
                                <div className={"statNumber"}>{this.getPendingSellers(sellers.sellers)}</div>
                                <div className={"statDetail"}>Sellers Pending Approval</div>
                            </div>
                        </div>
                        <div className={"col-md-3"}>
                            <div className={"sellerStat"}>
                                <div className={"statNumber"}>{this.getSellersMissingGst(sellers.sellers)}</div>
                                <div className={"statDetail"}>Seller Missing GST</div>
                            </div>
                        </div>
                    </div>
                    <div className={"row"}>
                        <div className={"col-md-12"}>
                            <div>
                                <div className={"tableTitle"} style={{display:'inline-block'}}>
                                    &nbsp;Seller Referral Leads
                                </div>
                                <Link to={"addSeller"}><Button type="primary" icon={<PlusOutlined />} shape="round" size={"large"} style={{display:'inline-block', marginLeft:'1rem'}}>
                                    Add Seller
                                </Button></Link>
                            </div>
                            <Table columns={this.state.columns} dataSource={this.getReferralSellers(sellers.sellers)} />
                            <div>
                                <div className={"tableTitle"} style={{display:'inline-block'}}>
                                    &nbsp;Seller Requests
                                </div>
                            </div>
                            <Table columns={this.state.pendingSellersRequestColumns} dataSource={sellers.pendingSellers}  pagination={{ pageSize: 6 }}/>

                            <div className={"tableTitle"} style={{display:'inline-block'}}>
                                    &nbsp;Approved Sellers
                                </div>&nbsp;&nbsp;&nbsp;&nbsp;
                            <Switch defaultChecked onChange={() => this.setState({showActiveOnly: !this.state.showActiveOnly})} />&nbsp;Show Active Only
                            <Table columns={this.state.approvedSellersColumns} dataSource={this.getApprovedSellers(sellers.sellers)}  pagination={{ pageSize: 6 }}/>

                        </div>
                    </div>
                </div>
                <Footer/>
            </div>
        );
    }
}

SellerManager.propTypes = {
    getSellers:PropTypes.func.isRequired,
    handleAuthentication:PropTypes.func.isRequired,
    updateSellerById:PropTypes.func.isRequired,
    getPendingSellers:PropTypes.func.isRequired,
    auth: PropTypes.object,
    sellers:PropTypes.object
}

const mapStateToProps = state  => ({
    auth:state.auth,
    sellers:state.sellers
})

export default connect(mapStateToProps, { getSellers, handleAuthentication, updateSellerById, getPendingSellers})(withRouter(SellerManager))

